<template>
  <el-card class="box-card">
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="username">
        <el-input placeholder="username" v-model="param.username"></el-input>
      </el-form-item>
      <el-form-item label="firstName">
        <el-input placeholder="firstName" v-model="param.firstName"></el-input>
      </el-form-item>
      <el-form-item label="lastName">
        <el-input placeholder="lastName" v-model="param.lastName"></el-input>
      </el-form-item>
      <el-form-item label="address">
        <el-input placeholder="address" v-model="param.address"></el-input>
      </el-form-item>
      <el-form-item label="numRestaurants">
        <el-input placeholder="numRestaurants" v-model="param.numRestaurants"></el-input>
      </el-form-item>
      <el-form-item label="numPlaces">
        <el-input placeholder="numPlaces" v-model="param.numPlaces"></el-input>
      </el-form-item>
      <el-form-item label="highs">
        <el-input placeholder="highs" v-model="param.highs"></el-input>
      </el-form-item>
      <el-form-item label="lows">
        <el-input placeholder="lows" v-model="param.lows"></el-input>
      </el-form-item>
      <el-form-item label="debt">
        <el-input placeholder="debt" v-model="param.debt"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="username"
          prop="username"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="firstName"
          prop="firstName"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="lastName"
          prop="lastName"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="address"
          prop="address"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="numRestaurants"
          prop="numRestaurants"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="numPlaces"
          prop="numPlaces"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="highs"
          prop="highs"
          sortable
      >
      </el-table-column>
      <el-table-column
